@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@400;500&display=swap);
/** @format */

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow: hidden;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/** @format */
.mainCoverGrid {
	display: grid;
	grid-template-columns: auto;
	grid-template-rows: repeat(4, 160px);
}
.mainCover {
	display: grid;
	grid-template-columns: repeat(5, 250px);
	grid-template-rows: repeat(5, 100px);
}
.mainHeader {
	grid-row: 2;
	grid-column: 2/4;
	font-size: 50px;
	font-family: Roboto;
	font-style: normal;
	font-weight: normal;
	font-size: 80px;
	line-height: 117px;
	color: #f8f1f1;
	text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.25);
}
.mainSubHeader {
	grid-row: 3;
	grid-column: 2/5;
	padding-top: 50px;
	font-family: Roboto;
	font-style: normal;
	font-weight: normal;
	font-size: 30px;
	/* line-height: 53px; */
	color: #f8f1f1;
	text-shadow: 0px 5px 4px rgba(0, 0, 0, 0.25);
}
.mainJoinButton {
	height: 30px;
	width: 150px;
}

.joinLink {
	text-decoration: none;
	grid-row: 5;
	grid-column: 2;
	align-self: center;
}

.card{background-color:#3d5a80;height:39.1rem;width:100%;display:flex;flex-direction:column;align-items:center;box-shadow:rgba(10,2,10,0.911);color:white}.card__name{margin-top:15px;font-size:1.5em}.card__image{height:120px;width:120px;border-radius:50%;border:5px solid #e0fbfc;margin-top:20px;box-shadow:0 10px 50px #0987c5}.cardList{padding:0;list-style:none;margin:1em}.cardList li{display:inline-block;margin:0.35em;position:relative;font-size:1em}.draw-border{box-shadow:inset 0 0 0 4px #e7ecec;color:#020a0e;transition:color 0.25s 0.0833333333s;position:relative}.draw-border::before,.draw-border::after{border:0 solid transparent;box-sizing:border-box;content:'';pointer-events:none;position:absolute;width:0rem;height:0;bottom:0;right:0}.draw-border::before{border-bottom-width:4px;border-left-width:4px}.draw-border::after{border-top-width:4px;border-right-width:4px}.draw-border:hover{color:#e3e2f1}.draw-border:hover::before,.draw-border:hover::after{border-color:#281ccf;transition:border-color 0s, width 0.25s, height 0.25s;width:100%;height:100%}.draw-border:hover::before{transition-delay:0s, 0s, 0.25s}.draw-border:hover::after{transition-delay:0s, 0.25s, 0s}.profileBtn{background:none;border:none;cursor:pointer;line-height:1;font:600 1.2rem 'Roboto Slab', sans-serif;padding:0.5em 1em;letter-spacing:0.05rem;margin:1em;width:8rem}.profileBtn:focus{outline:none}.grid-container{display:grid;grid-template-columns:1fr 1fr;grid-gap:20px;font-size:1.2em}

.avatar__image{
    height: 42px;
	width: 42px;
	border-radius: 50%;
}
